@import url(https://fonts.googleapis.com/css2?family=Martel:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, #root, .App {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  align-items: center;
}

header {
  width: 100%;
  height: 20px;
  background-color: #475665;
  box-shadow: 0px 3px 6px #000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: flex-start;
}

footer {
  width: 100%;
  height: 100px;
  background-color: #596B7E;
  box-shadow: 0px -3px 6px #000000;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer img {
  margin-left: 0.5rem;
}

footer a {
  display: flex;
  justify-content: center;
  align-items: center;
}

a {
  color: inherit;
  text-decoration: none;
}
/*# sourceMappingURL=App.css.map */
.visualizer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.main-visualizer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  border-radius: 5px;
  box-shadow: 4px 4px 8px black;
  background-color: #5F7286;
}

.chord-display-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 450px;
  border: 1px solid black;
  border-radius: 10px;
}

.note-display-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  height: 50px;
  width: 550px;
  margin: 5px;
  border: 1px solid black;
  border-radius: 5px;
}

#text {
  font-size: 50px;
  color: white;
  padding: 0;
  margin: 0;
}

#note-text {
  color: #229B05;
  font-size: 50px;
  padding: 10px;
  margin: 0;
}
/*# sourceMappingURL=visualizer.css.map */
.playArea {
  display: flex;
  justify-content: flex-end;
  flex-flow: column;
  align-items: center;
  height: 70%;
  background-color: #4a5d72;
}

.keyboard-container {
  display: flex;
  flex-direction: row;
}

.Keys {
  display: flex;
  align-self: center;
}

.white-key {
  position: relative;
  display: flex;
  align-self: center;
  justify-content: center;
  z-index: 1;
}

.white-key label {
  position: absolute;
  bottom: 20%;
  color: white;
}

.black-key {
  position: relative;
  z-index: 2;
  margin-left: -10px;
  margin-right: -10px;
}

.black-key label {
  position: absolute;
  left: 4px;
  top: 40%;
  font-size: 12px;
  color: white;
}

#pressedKey {
  background-color: yellow;
  opacity: 0.5;
}
/*# sourceMappingURL=keys.css.map */
.tonal-controller {
  background-color: #262525;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  box-shadow: 0px -1px 6px;
}

.tonal-main-container {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #475665;
  border-style: solid;
  border: 1px;
  border-color: black;
}

.Key-controller {
  padding: 0.5em;
}

.chordDisplay-main-container {
  flex: 3 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #4B5B6C;
}

#button {
  display: flex;
  padding: 10px 22px;
  margin: 4px 2px;
  text-align: center;
  text-decoration: none;
  background-color: #41556A;
  border: none;
  color: white;
  cursor: pointer;
}

#Labels {
  color: white;
}
/*# sourceMappingURL=tonalController.css.map */
/* PIANO PAGE CSS */
.container {
  min-height: 100vh;
  height: 100vh;
  width: 100%;
  display: flex;
  background-color: #526274;
  flex-direction: column;
}

.main-piano-container {
  background-color: #0070f3;
}

.content-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 50%;
  height: 100%;
  max-height: 1500px;
}
